import { IAuthData, IAuthDataResponse } from 'shared/interfaces/IAuth';
import { baseEmptyAPI } from './baseQuery';

export const authApi = baseEmptyAPI.injectEndpoints({
  endpoints: builder => ({
    login: builder.mutation<IAuthDataResponse, IAuthData>({
      query: body => ({
        url: 'https://dev-bnpl-users.apps.dev.bcc.kz/users/api/v1/sign-in',
        method: 'POST',
        body
      })
    })
  })
});

export const { useLoginMutation } = authApi;
